.post{
    width: 100%;
    border-radius: 20px;
    /* used https://cssgenerator.org/box-shadow-css-generator.html */
    box-shadow: 3px 6px 5px 0px rgba(0,9,28,0.73);
    -webkit-box-shadow: 3px 6px 5px 0px rgba(0,9,28,0.73);
    -moz-box-shadow: 3px 6px 5px 0px rgba(0,9,28,0.73);
    /* padding: 20px; */
    margin-bottom: 20px;
}

.postTop{
    display: flex;
    align-items: center;
    padding-top: 15px;
    padding-left: 1em;
    padding-right: 1em;
}

.postTopLeft{
    display: flex;
    align-items: center;
    width: 70%;
    float: left;
    margin-left: 0.5em;
    
}

.postTopRight{
    position: flex;
    align-items: center;
    float: right;
    width: 30%;
    margin-right: auto;
    /* margin-top: 0.5em; */
    display: flex; 
    justify-content: flex-end;
}



.postProfileName {
    font-size: 15px;
    font-family:Arial;
    display: flex;

}

.postProfilePic{
    object-fit:cover; /* maintain the ratio */
    margin-right: 20px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    
}

.postTime{
    font-size: 10px;
    margin: 0 10px;
    position: flex;
    align-items: center;
}

.postEdit{
    display: flex;
    align-items: center;
    box-shadow:inset 0px 1px 0px 0px #ffffff;
	background:linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
	background-color:#ffffff;
	border-radius:6px;
	border:1px solid #dcdcdc;
	display:inline-block;
	cursor:pointer;
	color:#666666;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;
}
.postEdit:hover {
	background:linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
	background-color:#f6f6f6;
}
.postEdit:active {
	position:relative;
	top:1px;
}

.postMain{
    /* margin: 10px; */
    /* margin-top: 0.5em;
    margin-bottom: 0.5em; */
}

.postMainPic{
    width: 100%;
    margin-top: 1em;
    margin-bottom: 1em;
    /* padding: px; */
}

.postMainCaptions{ 
    /* padding: 20px; */
}

.postBot{
    display: flex;
    align-items: center;
    padding-bottom: 15px;
    padding-left: 1em;
    padding-right: 1em;
}

.postBotLeft{
    display: flex;
    align-items: center;
    width: 50%;
    /* margin-left: 0.5em */
    margin-left: 0; 
    margin-right: auto;
}

.postBotRight{
    position: flex;
    align-items: center;
    width: 50%;
    margin-left: auto;
    display: flex; 
    justify-content: flex-end;
}

.postComment{
    display: flex;
    align-items: center;
    box-shadow:inset 0px 1px 0px 0px #ffffff;
	background:linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
	background-color:#ffffff;
	border-radius:6px;
	border:1px solid #dcdcdc;
	display:inline-block;
	cursor:pointer;
	color:#666666;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;
}

.postComment:hover {
	background:linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
	background-color:#f6f6f6;
}
.postComment:active {
	position:relative;
	top:1px;
}